import {Component} from '@angular/core';
import {IonicPage, NavController} from 'ionic-angular';
import {FirebaseProvider} from "../../providers/firebase/firebase";
import {FirebaseListObservable} from "angularfire2/database";
import {Article} from "../../providers/model/article";
import {ArticleService} from "../../providers/article.service";
import {UserService} from "../../providers/user.service";
import {Service} from "../../providers/model/service";

@IonicPage()
@Component({
  selector: 'page-mystore',
  templateUrl: 'mystore.html',
})
export class MyStorePage {

  type: string = "published";

  product: FirebaseListObservable<Article[]>;
  productSold: FirebaseListObservable<Article[]>;
  services: FirebaseListObservable<Service[]>;
  alive: boolean = true;


  constructor(public userService: UserService,
              public firebase: FirebaseProvider,
              private navCtrl: NavController,
              private articleService: ArticleService) {

    this.product = this.articleService.getMyArticles(this.userService._userFirebase.uid);
    this.productSold = this.articleService.getMyArticles(this.userService._userFirebase.uid);
    this.services = this.articleService.getMyServices(this.userService._userFirebase.uid);
  }

  ionViewDidLoad() {

  }

  ionViewWillLeave() {
    this.alive = false;
  }

  seeDetails(item, key, isInventory) {
    if(isInventory) {
      this.navCtrl.push('ArticlePage',
        {article: item, mine: true, isInventory: isInventory});
    } else {
      this.navCtrl.push('ArticlePage',
        {article: item, key: key, mine: true});
    }
  }

  seeDetailsService(item) {
    this.navCtrl.push('ServicePage',
      {service: item, mine: true, key: item.$key});
  }

}



